#graphic_ctn {
  min-height: 100vh;
  width: 100vw;
}
#graphic_ctn div,
#graphic_ctn canvas {
  min-height: 100vh;
}
.inline-media-container {
  width: 90%;
  max-width: 40em;
  max-height: 40em;
  margin: auto;
}
.inline-media-container img,
.inline-media-container video {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.caption {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #fff;
  text-align: left;
  font-style: italic;
  font-size: 0.8em;
  line-height: 1.2em;
}
#intro {
  position: relative;
  height: auto;
}
#intro video {
  position: absolute;
  top: 0;
  height: calc(100vh - 55px);
  width: 100%;
  object-fit: cover;
}
#intro-video-container {
  top: 55px;
  position: -webkit-sticky;
  position: sticky;
  height: calc(100vh - 55px);
  background-color: #000;
}
#intro-base-video {
  opacity: 0;
}
#title,
#subtitle {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 1em;
  position: relative;
  top: calc(55px - 50vh);
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #fff;
  text-align: center;
  font-size: 2em;
}
@media only screen and (min-width: 40em) {
  #title,
  #subtitle {
    margin: 0 auto;
    font-size: 3em;
  }
}
#byline {
  position: relative;
  top: calc(55px - 50vh);
  text-align: center;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 0.8em;
  margin: 40px auto auto 0px;
}
@media only screen and (min-width: 40em) {
  #byline {
    margin: 0 auto;
    font-size: 1.2em;
  }
}
#intro-scrolly-container {
  position: relative;
  z-index: 10;
  width: 100%;
  margin-top: -50vh;
}
#intro-scrolly-container :first-of-type ::first-line {
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  color: #a81854;
  font-style: normal;
  font-weight: 700;
  font-size: 2em;
}
.intro-scrolly-text {
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10vh;
  width: 80%;
  max-width: 40em;
  padding: 10px;
  color: #fff;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1em;
  transform: translate(0, -50%);
}
.intro-scrolly-text:nth-last-child(2) {
  margin-bottom: 80vh;
}
@media only screen and (min-width: 40em) {
  .intro-scrolly-text {
    font-size: 1.2em;
  }
}
main {
  width: 100%;
  height: 100%;
  padding-top: 55px;
  box-sizing: border-box;
  position: relative;
  z-index: 0;
}
.text-section p {
  width: 80%;
  margin: 1.5em auto;
  max-width: 40em;
  font-size: 1.2em;
  line-height: 1.3em;
}
.credits-section {
  width: 100%;
  margin: 3em auto;
  max-width: 40em;
}
.credits {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.2em;
  line-height: 1.2em;
}
.credits a {
  color: #fff;
  text-decoration: underline;
}
#credit-title {
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  text-align: center;
  font-size: 2.4em;
}
@media only screen and (max-width: 700px) {
  #title-container {
    padding-top: 20vh;
  }
  .text-section p {
    width: 92%;
    margin: 1.5em auto;
  }
  .credits-section {
    width: 92%;
  }
}
#navbar {
  overflow: hidden;
  background-color: #000;
  height: 55px;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  z-index: 10;
}
#navbar img {
  height: 100%;
  box-sizing: border-box;
}
.navbar-logo {
  display: flex;
  flex-flow: row;
  margin-left: 0.2em;
}
.navbar-social {
  height: 100%;
  display: flex;
  flex-flow: row;
  padding-right: 5px;
}
.navbar-social .nav-soc {
  max-width: 40px;
}
.navbar-social .nav-soc img {
  width: 100%;
}
.button-container {
  margin-left: 1em;
  margin-top: 1em;
}
@media only screen and (min-width: 40em) {
  .social-button {
    display: inline;
  }
}
.sub-button {
  background-color: #a81854;
  color: #fff;
  padding: 4px;
  border-radius: 5px;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 0.9em;
}
.search form {
  max-width: 150px;
  margin-top: 10px;
}
.search form div {
  height: 30px;
  color: #000;
}
.search form div fieldset {
  border-color: #000 !important;
}
.njdc-links {
  margin: auto 10px;
  font-size: 0.8em;
}
@media only screen and (min-width: 40em) {
  .njdc-links {
    font-size: 1em;
    margin: auto 15px;
  }
}
@media only screen and (min-width: 60em) {
  .njdc-links {
    font-size: 1.2em;
  }
}
.njdc-links a {
  color: #000;
  font-family: "Merriweather", serif;
  font-weight: 400;
  font-size: 0.7em;
}
.njdc-links a:after {
  padding: 5px;
  content: "|";
}
@media only screen and (min-width: 40em) {
  .njdc-links a:after {
    padding: 10px;
  }
}
.njdc-links a:last-child::after {
  content: "";
}
.njdc-links a:hover {
  text-decoration: underline;
  -webkit-text-decoration-color: #a81854;
          text-decoration-color: #a81854;
  text-decoration-thickness: 20px;
  font-style: italic;
}
.sub-button-desktop {
  display: inline-block;
  background-color: #c6d653;
  border: solid 2px #c6d653;
  border: none;
  color: #000;
  margin-top: 10px;
  padding: 0.3em;
  border-radius: 5px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  font-size: 0.5em;
  padding: 0.5em;
  font-size: 0.8em;
}
.scrolly-section {
  position: relative;
}
.scrolly-image-container {
  position: -webkit-sticky;
  position: sticky;
  top: 55px;
  width: 100%;
  height: calc(100vh - 55px);
  z-index: 0;
}
.scrolly-image {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 500ms 500ms;
}
.scrolly-image.visible {
  opacity: 1;
  transition: opacity 500ms;
}
.scrolly-text-container {
  position: relative;
  z-index: 10;
  padding-bottom: 1px;
}
.scrolly-text {
  width: 80%;
  max-width: 40em;
  margin: 0 auto;
  border: solid #000 1px;
  padding: 1em;
  background-color: #000;
  font-size: 1.2em;
}
.scrolly-text:not(:first-child) {
  margin: 100vh auto;
}
.video-section {
  position: relative;
  height: calc(100vh - 55px);
}
.video-section video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-section h2 {
  position: absolute;
  top: calc((100vh - 55px) / 2);
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.4em;
  z-index: 10;
  text-align: center;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #fff;
}
@media only screen and (min-width: 40em) {
  .video-section h2 {
    font-size: 3em;
    text-align: left;
  }
}
.video-overlay {
  z-index: 5;
}
html,
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  background-color: #000;
  width: 100%;
  height: 100%;
  font-family: 'Open Sans', sans-serif;
}
html,
body,
#root,
.App {
  width: 100%;
  height: 100%;
}
a {
  text-decoration: none;
}
